"use strict";
import {getObjSize} from './ObjectUtils.js';
import {getRandIntFrom} from './RandUtils.js';
/**
 * nums       .<br/>
 * nums   1 ,       false .
 * @param {...number} nums
 * @returns {boolean}
 */
export const areNumsUnique = (...nums) => {
    if (nums.length <= 1)
        return false;
    let numSet = {};
    for (let num of nums) {
        numSet[num] = true;
    } //num loop
    return getObjSize(numSet) === nums.length;
};
/**
 * nums       .<br/>
 * nums   1 ,     false .
 * @param {...number} nums
 * @returns {boolean}
 */
export const areNumsIdentical = (...nums) => {
    if (nums.length <= 1)
        return false;
    let numSet = {};
    for (let num of nums) {
        numSet[num] = true;
    } //num loop
    return getObjSize(numSet) === 1;
};
/**
 * nums     .<br/>
 * nums       .
 * @param {...number} nums
 * @returns {boolean}
 */
export const areNumsConsecutive = (...nums) => {
    if (!(areNumsUnique(...nums)))
        return false;
    let minNum = nums[0];
    let maxNum = nums[0];
    for (let num of nums) {
        if (!(Number.isInteger(num))) {
            throw new Error("The function must take an array of integers.");
        }
        if (num > maxNum)
            maxNum = num;
        else if (num < minNum)
            minNum = num;
    } //num loop
    return (maxNum - minNum) === (nums.length - 1);
};
/**
 *         .
 * @param {Array} arr0
 * @param {Array} arr1
 * @returns {boolean}
 */
export const areTwoArrsIdentical = (arr0, arr1) => {
    if (arr0.length !== arr1.length)
        return false;
    for (let i = 0; i < arr0.length; i++) {
        switch ((typeof arr0[i]).toLowerCase()) {
            case 'number':
            case 'string':
            case 'boolean':
            case 'undefined':
                if (arr0[i] !== arr1[i])
                    return false;
                break;
            default:
                if (!(arr0[i].equals(arr1[i])))
                    return false;
                break;
        }
    } //i loop
    return true;
};
/**
 *       .
 * @param {Array} arr
 * @returns {boolean}
 */
export const isArrEmpty = (arr) => {
    return arr.length === 0;
};
/**
 *    . (Fisher-Yates algorithm)
 * @param {Array} arr   (     .)
 */
export const shuffleArray = (arr) => {
    for (let i = arr.length - 1; i > 0; i--) {
        let j = getRandIntFrom(i + 1); //random index from 0 to i
        [arr[i], arr[j]] = [arr[j], arr[i]]; //destructuring assignment syntax to swap element arr[i] & arr[j]
    } //i loop
};
/**
 *   .
 * @param {number} length    . 0  range   .
 * @param {number} range   . 1   .
 * @returns {number[]}  .   0  range  .
 */
export const getRandArray = (length, range) => {
    if (!(Number.isInteger(length)) || length < 0 || length > range) {
        throw new Error("The method must take an integer length between 0 and range.");
    }
    if (!(Number.isInteger(range)) || range < 1) {
        throw new Error("The method must take an integer range greater than 0.");
    }
    let randArray = new Array(range);
    for (let i = 0; i < range; i++) {
        randArray[i] = i;
    } //i loop
    shuffleArray(randArray);
    return randArray.splice(0, length); //randArray   .
};
