"use strict";
/**
 *   .
 * @param {number} bound     .     0    ,
 *     0     .
 * @returns {number}
 */
export const getRandIntFrom = (bound) => {
    return Math.trunc(Math.random() * bound);
};
/**
 *       .   1.2 1 2  <br/>
 *    1  2 . 1.2 2 1   <br/>
 *    1    .
 * @param {number} num
 * @returns {number}
 */
export const probabilisticRound = (num) => {
    let intPart = Math.floor(num);
    let fracPart = num - intPart;
    if (Math.random() < fracPart) {
        return intPart + 1;
    }
    else {
        return intPart;
    }
};
