"use strict";
import {toBinaryArray, setOnBitAt} from './BitUtils.js';
import {TileState, Tile} from './Tile.js';
import {Dice} from './Dice.js';
import {HandScore} from './HandScore.js';
/**
 *   
 * @author sianke1991
 */
export class Participant {
    /**
     *     
     * @param {number} ordinal             
     * @param {string} name              
     * @param {number} accumScore        
     * @param {HandScore} handScore       
     * @param {number} numRolls           
     * @param {Tile[]} tiles               
     * @param {TileState[]} tileStates     
     * @param {nunber} diceSelected      
     */
    constructor(ordinal, name, accumScore, handScore, numRolls, tiles, tileStates, diceSelected) {
        this.ordinal = ordinal;
        this.name = name;
        this.accumScore = accumScore;
        this.handScore = handScore;
        this.numRolls = numRolls;
        this.tiles = tiles;
        this.tileStates = tileStates;
        this.diceSelected = diceSelected;
        this.ordinal = ordinal;
        this.name = name;
        this.accumScore = accumScore;
        this.handScore = handScore;
        this.numRolls = numRolls;
        this.tiles = tiles;
        this.tileStates = tileStates;
        this.diceSelected = diceSelected;
    }
    static values() {
        return [
            Participant.PLAYER,
            Participant.OPPONT1,
            Participant.OPPONT2,
            Participant.OPPONT3
        ];
    }
    static valueOf(name) {
        switch (name) {
            case Participant.PLAYER.toString():
                return Participant.PLAYER;
            case Participant.OPPONT1.toString():
                return Participant.OPPONT1;
            case Participant.OPPONT2.toString():
                return Participant.OPPONT2;
            case Participant.OPPONT3.toString():
                return Participant.OPPONT3;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    static getInstance(ordinal) {
        switch (ordinal) {
            case Participant.PLAYER.ordinal:
                return Participant.PLAYER;
            case Participant.OPPONT1.ordinal:
                return Participant.OPPONT1;
            case Participant.OPPONT2.ordinal:
                return Participant.OPPONT2;
            case Participant.OPPONT3.ordinal:
                return Participant.OPPONT3;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case Participant.PLAYER.ordinal:
                return "PLAYER";
            case Participant.OPPONT1.ordinal:
                return "OPPONT1";
            case Participant.OPPONT2.ordinal:
                return "OPPONT2";
            case Participant.OPPONT3.ordinal:
                return "OPPONT3";
            default:
                return "INVALID_OBJECT";
        }
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
    /**
     *    . TileState  NOT_DETERMINED, diceSelected 0    .<br/>
     *   1 .
     * @param {Rules} rules
     */
    roll(rules) {
        for (let i = 0; i < this.tileStates.length; i++) {
            this.tileStates[i] = TileState.NOT_DETERMINED;
        }
        this.diceSelected = 0;
        for (let i = 0; i < this.tiles.length; i++) {
            this.tiles[i] = Dice.getRandomTileAt(i, rules);
        }
        this.numRolls = 1;
    }
    /**
     *  ()   .<br/>
     * NOW_SELECTING   ALREADY_SELECTED  , diceSelected  bit 1 .<br/>
     * NOT_SELECTED   NOT_DETERMINED    .<br/>
     * shouldPreserveNumRolls false    1 .<br/>
     * *         ALREADY_SELECTED  NOT_DETERMINED  .
     * @param {Rules} rules
     * @param {boolean} shouldPreserveNumRolls
     */
    reroll(rules, shouldPreserveNumRolls) {
        for (let i = 0; i < this.tileStates.length; i++) {
            switch (this.tileStates[i].ordinal) {
                case TileState.NOW_SELECTING.ordinal:
                    this.tileStates[i] = TileState.ALREADY_SELECTED;
                    this.diceSelected = setOnBitAt(i, this.diceSelected);
                    break;
                case TileState.NOT_SELECTED.ordinal:
                    this.tileStates[i] = TileState.NOT_DETERMINED;
                    this.tiles[i] = Dice.getRandomTileAt(i, rules);
                    break;
            }
        }
        if (!shouldPreserveNumRolls)
            this.numRolls++;
    }
    /**
     *     NOT_DETERMINED   NOT_SELECTED  .<br/>
     * *         ALREADY_SELECTED  NOT_SELECTED  .
     */
    reveal() {
        for (let i = 0; i < this.tileStates.length; i++) {
            if (TileState.NOT_DETERMINED.equals(this.tileStates[i])) {
                this.tileStates[i] = TileState.NOT_SELECTED;
            }
        }
    }
    /**
     * 0 7     TileState .(NOT_SELECTED <-toggle-> NOW_SELECTING) (PLAYER )<br/>
     *  (rules.usageOfFlowers) MUST_BE_REROLLED,     TileState   1 .<br/>
     *    ALREADY_SELECTED, NOT_DETERMINED  TileState   2 .<br/>
     *    TileState   0 .
     * @param {number} idx
     * @param {Rules} rules
     * @returns {number}
     */
    toggleTileStateAt(idx, rules) {
        if (this.tiles[idx].isUnselectable(rules))
            return 1;
        switch (this.tileStates[idx].ordinal) {
            case TileState.ALREADY_SELECTED.ordinal:
            case TileState.NOT_DETERMINED.ordinal:
                return 2;
            case TileState.NOT_SELECTED.ordinal:
                this.tileStates[idx] = TileState.NOW_SELECTING;
                return 0;
            case TileState.NOW_SELECTING.ordinal:
                this.tileStates[idx] = TileState.NOT_SELECTED;
                return 0;
        }
        return -1;
    }
    /**
     *   () TileState  (NOT_SELECTED -> NOW_SELECTING)(OPPONT )<br/>
     * @param {number} diceSelecting
     */
    selectDiceAt(diceSelecting) {
        let selectingBitArr = toBinaryArray(diceSelecting);
        for (let i = 0; i < selectingBitArr.length; i++) {
            if (selectingBitArr[i] !== 0 && TileState.NOT_SELECTED.equals(this.tileStates[i])) {
                this.tileStates[i] = TileState.NOW_SELECTING;
            }
        } //i loop
    }
    /**
     *        .<br/>
     *     ,          0 .<br/>
     * FlowerLock  0 .<br/>
     * (FlowerLock       FlowerLock   , FlowerLock     .)
     * @param {Rules} rules
     * @param {number} depth
     * @returns {number}
     */
    getOptReroll(rules, depth) {
        return Dice.optReroll(rules, this.numRolls, depth, this.diceSelected, ...this.tiles);
    }
    /**
     *        .
     * @returns {number}
     */
    getDefaultDepth() {
        let numNotSelected = 0;
        for (let tileState of this.tileStates) {
            if (TileState.NOT_SELECTED.equals(tileState))
                numNotSelected++;
        }
        switch (numNotSelected) {
            case 8:
                return 1.0;
            case 7:
                return 1.1;
            case 6:
                return 1.2;
            case 5:
                return 1.3;
            case 4:
                return 1.4;
            case 3:
                return 1.5;
            case 2:
                return 1.6;
            case 1:
                return 1.7;
            default:
                return 1.8;
        }
    }
    /**
     *             .
     * @param {Rules} rules
     * @returns {boolean}
     */
    isFlowerLock(rules) {
        return Dice.isFlowerLock(rules, this.diceSelected, ...this.tiles);
    }
    /**
     *      ,<br/>
     * ,          .<br/>
     *          .
     * @param {Rules} rules
     * @returns {boolean}
     */
    isUnableToReroll(rules) {
        return Dice.isUnableToReroll(rules, this.numRolls, this.diceSelected);
    }
    /**
     *     handScore .
     * @param {Rules} rules
     */
    scoreHand(rules) {
        this.handScore = HandScore.scoreHand(rules, this.numRolls, ...this.tiles);
    }
    /**
     *             .<br/>
     *         .
     * @returns {boolean}
     */
    isHandScorePositive() {
        return this.handScore.totalScore > 0;
    }
    /**
     *      URL .
     * @param {number} idx
     * @returns {string}
     */
    getImageURLOfTileAt(idx) {
        return this.tiles[idx].toImageURL(this.tileStates[idx]);
    }
    /**
     *   handScore.totalScore .
     */
    increaseAccumScore() {
        if (this.isHandScorePositive()) {
            this.accumScore += this.handScore.totalScore;
        }
    }
    /**
     *      .
     */
    resetForNewRound() {
        this.handScore = HandScore.getDefault();
        this.numRolls = 0;
        for (let i = 0; i < this.tileStates.length; i++)
            this.tileStates[i] = TileState.NOT_DETERMINED;
        this.diceSelected = 0;
    }
    /**
     *      .
     */
    resetForNewGame() {
        this.accumScore = 0;
        this.handScore = HandScore.getDefault();
        this.numRolls = 0;
        for (let i = 0; i < this.tiles.length; i++)
            this.tiles[i] = Tile.DOT1;
        for (let i = 0; i < this.tileStates.length; i++)
            this.tileStates[i] = TileState.NOT_DETERMINED;
        this.diceSelected = 0;
    }
    /**
     *  ( )     .
     * @returns {boolean}
     */
    isSelectingDice() {
        let notSelectedIdx = -1, nowSelectingIdx = -1;
        for (let i = 0; i < this.tileStates.length; i++) {
            switch (this.tileStates[i].ordinal) {
                case TileState.NOT_SELECTED.ordinal:
                    notSelectedIdx = i;
                    break;
                case TileState.NOW_SELECTING.ordinal:
                    nowSelectingIdx = i;
                    break;
            }
        } //i loop
        return notSelectedIdx >= 0 && nowSelectingIdx >= 0;
    }
}
Participant.PLAYER = new Participant(0, "", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
Participant.OPPONT1 = new Participant(1, "1", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
Participant.OPPONT2 = new Participant(2, "2", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
Participant.OPPONT3 = new Participant(3, "3", 0, HandScore.getDefault(), 0, [Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1, Tile.DOT1], [TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED, TileState.NOT_DETERMINED], 0);
