export class TileState {
    constructor(ordinal) {
        this.ordinal = ordinal;
        this.ordinal = ordinal;
    }
    static values() {
        return [
            TileState.NOT_DETERMINED,
            TileState.NOT_SELECTED,
            TileState.NOW_SELECTING,
            TileState.ALREADY_SELECTED
        ];
    }
    static valueOf(name) {
        switch (name) {
            case TileState.NOT_DETERMINED.toString():
                return TileState.NOT_DETERMINED;
            case TileState.NOT_SELECTED.toString():
                return TileState.NOT_SELECTED;
            case TileState.NOW_SELECTING.toString():
                return TileState.NOW_SELECTING;
            case TileState.ALREADY_SELECTED.toString():
                return TileState.ALREADY_SELECTED;
            default:
                throw new Error("The method takes an invalid argument.");
        }
    }
    toString() {
        switch (this.ordinal) {
            case TileState.NOT_DETERMINED.ordinal:
                return "NOT_DETERMINED";
            case TileState.NOT_SELECTED.ordinal:
                return "NOT_SELECTED";
            case TileState.NOW_SELECTING.ordinal:
                return "NOW_SELECTING";
            case TileState.ALREADY_SELECTED.ordinal:
                return "ALREADY_SELECTED";
            default:
                return "INVALID_OBJECT";
        }
    }
    equals(that) {
        return this.ordinal === that.ordinal;
    }
}
