"use strict";
import {getRandIntFrom} from './RandUtils.js';
import {UsageOfFlowers, MaxRollsPerRound, GameLength, Rules} from './Rules.js';
import {Tile} from './Tile.js';
import {HandScore} from './HandScore.js';
import {Participant} from './Participant.js';
/*
         (class)  .
     .
Rules:         
RoundState:   (    )   
  (     )
GameState:            
*/
/**
 *      
 * @author sianke1991
 */
export class RoundState {
    /**
     *   
     * @param {number} roundNumber   (0 ,    )
     * @param {number} dealerNumber     (0 ,    )
     */
    constructor(roundNumber, dealerNumber) {
        this.roundNumber = roundNumber;
        this.dealerNumber = dealerNumber;
        this.roundNumber = roundNumber;
        this.dealerNumber = dealerNumber;
    }
    toString() {
        return `{dataType: RoundState, roundNumber: ${this.roundNumber}, dealerNumber: ${this.dealerNumber}}`;
    }
    equals(that) {
        return (this.roundNumber === that.roundNumber &&
            this.dealerNumber === that.dealerNumber);
    }
    /**
     *    .
     * @returns {RoundState}
     */
    static getDefault() {
        return new RoundState(RoundState.DEFAULT_ROUND_NUMBER, RoundState.DEFAULT_DEALER_NUMBER);
    }
    toDealerName() {
        return Participant.getInstance(this.dealerNumber).name;
    }
    /**
     *      .<br/>
     *     0,    1 .
     * @param {Rules} rules
     * @returns {number}
     */
    resetForNewRound(rules) {
        let nextRoundNumber = this.roundNumber + 1;
        if (nextRoundNumber >= rules.gameLength.numRounds)
            return 1;
        let nextDealerNumber = (this.dealerNumber + 1) % rules.gameLength.numParticipants;
        this.roundNumber = nextRoundNumber;
        this.dealerNumber = nextDealerNumber;
        for (let participant of Participant.values()) {
            participant.resetForNewRound();
        }
        return 0;
    }
    /**
     *      .
     * @param {Rules} rules
     */
    resetForNewGame(rules) {
        this.roundNumber = 0;
        this.dealerNumber = getRandIntFrom(rules.gameLength.numParticipants);
        for (let participant of Participant.values()) {
            participant.resetForNewGame();
        }
    }
}
RoundState.DEFAULT_ROUND_NUMBER = -1;
RoundState.DEFAULT_DEALER_NUMBER = 0;
/**
 *      
 * @author sianke1991
 */
export class GameState {
    /**
     *    
     * @param {number} stateCode   (0  9999  )
     * @param {number} previousStateCode    (0  9999  )
     */
    constructor(stateCode, previousStateCode) {
        this.stateCode = stateCode;
        this.previousStateCode = previousStateCode;
        this.stateCode = stateCode;
        this.previousStateCode = previousStateCode;
    }
    toString() {
        return `{dataType: GameState, stateCode: ${this.stateCode}, previousStateCode: ${this.previousStateCode}}`;
    }
    equals(that) {
        return (this.stateCode === that.stateCode &&
            this.previousStateCode === that.previousStateCode);
    }
    /**
     *     .
     * @returns {GameState}
     */
    static getDefault() {
        return new GameState(GameState.INVALID_STATE_CODE, GameState.INVALID_STATE_CODE);
    }
    /**
     * messageArea   .<br/>
     *          &nbsp; .
     * @returns {string}
     */
    toMessage() {
        switch (this.stateCode) {
            case 1:
                return '      .<br/><span class="gray borderGray"></span>  .<br/>   .';
            case 1000:
            case 1100:
            case 1200:
            case 1300: {
                let currentParticipantNumber = this.getCurrentParticipantNumber();
                if (currentParticipantNumber === null)
                    return "<br/><br/>";
                else
                    return `${Participant.getInstance(currentParticipantNumber).name}  .<br/><span class="blue borderBlue"></span>    .`;
            }
            case 1003:
            case 1103:
            case 1203:
            case 1303:
                return '  .<br/><span class="blue borderBlue"></span>    .';
            case 1004:
                return '<span class="blue borderBlue">&nbsp;</span>  .';
            case 1005:
                return '    <span class="blue borderBlue">&nbsp;</span>  .<br/><span class="red">   .<br/> </span> <span class="blue borderBlue">&nbsp;</span> <span class="red"> .</span>';
            case 1006:
            case 1106:
            case 1206:
            case 1306:
                return '     .<br/><span class="blue borderBlue"></span>    .';
            case 1007:
                return '    <span class="blue borderBlue">&nbsp;</span>  .';
            case 1009:
            case 1109:
            case 1209:
            case 1309:
                return '   .<br/><span class="blue borderBlue"></span>    .';
            case 2001:
                return ' .<br/><span class="gray borderGray"></span>         .<br/>   .';
            default:
                return "<br/><br/>";
        }
    }
    static toMessage(errorCode) {
        switch (errorCode) {
            case 1:
                return "       .";
            case 2:
                return "     .";
            default:
                return "";
        }
    }
    toCurrentParticipantName() {
        let currentParticipantNumber = this.getCurrentParticipantNumber();
        if (currentParticipantNumber === null)
            return "";
        else
            return Participant.getInstance(currentParticipantNumber).name;
    }
    slowlySetTo(stateCode) {
        this.previousStateCode = this.stateCode;
        this.stateCode = GameState.INVALID_STATE_CODE;
        setTimeout(() => { this.stateCode = stateCode; }, 500);
    }
    quicklySetTo(stateCode) {
        this.previousStateCode = this.stateCode;
        this.stateCode = GameState.INVALID_STATE_CODE;
        setTimeout(() => { this.stateCode = stateCode; }, 250);
    }
    reset() {
        this.previousStateCode = this.stateCode;
        this.stateCode = GameState.INVALID_STATE_CODE;
        this.stateCode = this.previousStateCode;
    }
    /**
     *            .
     */
    static setMemoizationCaches() {
        let rules = new Rules(UsageOfFlowers.CAN_BE_WILD, MaxRollsPerRound.getDefault(), GameLength.getDefault());
        let tiles = [Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER, Tile.JOKER];
        HandScore.scoreHand(rules, Number.POSITIVE_INFINITY, ...tiles);
        let concreteTiles = Tile.getTileListWildCanBe(rules);
        for (let tile of concreteTiles) {
            tiles[0] = tile;
            HandScore.scoreHand(rules, Number.POSITIVE_INFINITY, ...tiles);
        }
    }
    /**
     *     .<br/>
     *      null .
     * @returns {number|null}
     */
    getCurrentParticipantNumber() {
        if (this.stateCode !== GameState.INVALID_STATE_CODE) {
            switch (Math.trunc(this.stateCode / 100)) {
                case 10:
                    return 0;
                case 11:
                    return 1;
                case 12:
                    return 2;
                case 13:
                    return 3;
                default:
                    return null;
            }
        }
        else {
            switch (Math.trunc(this.previousStateCode / 100)) {
                case 10:
                    return 0;
                case 11:
                    return 1;
                case 12:
                    return 2;
                case 13:
                    return 3;
                default:
                    return null;
            }
        }
    }
    /**
     *          .<br/>
     * ,  [ ]     null .
     * @param {Rules} rules
     * @param {RoundState} roundState
     * @returns {number|null}
     */
    getNextParticipantNumber(rules, roundState) {
        let currentParticipantNumber = this.getCurrentParticipantNumber();
        if (currentParticipantNumber === null)
            return null;
        let numParticipants = rules.gameLength.numParticipants;
        let dealerNumber = roundState.dealerNumber;
        let nextParticipantNumber = (currentParticipantNumber + 1) % numParticipants;
        if (nextParticipantNumber === dealerNumber)
            return null;
        else
            return nextParticipantNumber;
    }
    isLoading() {
        return this.stateCode === 0;
    }
    isRerollButtonActive() {
        switch (this.stateCode) {
            case 1005:
            case 1007:
                return Participant.PLAYER.isSelectingDice();
            default:
                return false;
        }
    }
    isGoOutButtonActive() {
        switch (this.stateCode) {
            case 1004:
            case 1005:
                return !Participant.PLAYER.isSelectingDice();
            default:
                return false;
        }
    }
    isConfirmButtonActive() {
        switch (this.stateCode) {
            case 1000:
            case 1100:
            case 1200:
            case 1300:
            case 1003:
            case 1103:
            case 1203:
            case 1303:
            case 1006:
            case 1106:
            case 1206:
            case 1306:
            case 1009:
            case 1109:
            case 1209:
            case 1309:
                return true;
            default:
                return false;
        }
    }
    isResetButtonActive() {
        switch (this.stateCode) {
            case 1:
            case 2001:
                return true;
            default:
                return false;
        }
    }
}
GameState.INVALID_STATE_CODE = -1;
